package casper.signal

class PromiseUnion<Accept, Reject>(vararg promiseList: Promise<out Accept, out Reject>) : Promise<List<Accept>, List<Reject>> {
	private var block = true
	private var amount = 0
	private val common = SinglePromiseSignal<List<Accept>, List<Reject>>()
	private val acceptList = mutableListOf<Accept>()
	private val rejectList = mutableListOf<Reject>()

	init {
		promiseList.forEach {
			add(it)
		}
		block = false
		check()
	}

	fun add(promise: Promise<out Accept, out Reject>) {
		amount++
		promise.then(::onAccept, ::onReject)
	}


	fun onAccept(accept: Accept) {
		acceptList.add(accept)
		check()
	}

	fun onReject(reject: Reject) {
		rejectList.add(reject)
		check()
	}

	private fun check() {
		if (!block && acceptList.size + rejectList.size == amount) {
			complete()
		}
	}

	private fun complete() {
		if (rejectList.isNotEmpty()) {
			common.reject(rejectList)
		} else {
			common.accept(acceptList)
		}
	}

	override fun acceptFuture(): Future<List<Accept>> {
		return common.acceptFuture()
	}

	override fun rejectFuture(): Future<List<Reject>> {
		return common.rejectFuture()
	}

	override fun isCompleted(): Boolean {
		return common.isCompleted()
	}

}